import { mapState } from 'vuex';
const Cookie = process.client ? require('js-cookie') : null
export default {
    name: "LayoutIndex",
    computed: {
        ...mapState(['user']),
    },
    methods: {
        logOut() {
            this.$store.commit('setUser', null)
            Cookie.set('auth', null)
        }
    }
}
